import pinyinUtil from './pinyinUtil'

export const pySort= (arr) =>{
        // var letters = "ABCDEFGHJKLMNOPQRSTWXYZ".split('');
        // letters = letters.reverse()
        // console.log(letters)
        var result = {};
        // for(var i=0;i<letters.length;i++){
                for(var j =0;j<arr.length;j++){
                    var firstLetter = pinyinUtil.getFirstLetter(arr[j].get('name'))
                    var initial = firstLetter.charAt(0);//截取第一个字符
                    if(!result[initial]){
                        result[initial] = []
                    }
                    // if(initial == letters[i]){
                    //     console.log(initial)
                        result[initial].push(
                            {
                                name:arr[j].get('name'),
                                imgUrl:arr[j].get('img') || "https://ygz.msit.pw/img/favicon.ico",
                                id:arr[j].id,
                                user:arr[j]
                            }
                        )
                    }
                    
                // }
            
        // }
        return result;
}
/**
 * 存储localStorage
 */
export const setStore = (name, content) => {
	if (!name) return;
	if (typeof content !== 'string') {
		content = JSON.stringify(content);
	}
	window.localStorage.setItem(name, content);
}

/**
 * 获取localStorage
 */
export const getStore = name => {
	if (!name) return;
	return window.localStorage.getItem(name);
}

/**
 * 删除localStorage
 */
export const removeStore = name => {
	if (!name) return;
	window.localStorage.removeItem(name);
}

/**
 * 获取style样式
 */
export const getStyle = (element, attr, NumberMode = 'int') => {
    let target;
    // scrollTop 获取方式不同，没有它不属于style，而且只有document.body才能用
    if (attr === 'scrollTop') { 
        target = element.scrollTop;
    }else if(element.currentStyle){
        target = element.currentStyle[attr]; 
    }else{ 
        target = document.defaultView.getComputedStyle(element,null)[attr]; 
    }
    //在获取 opactiy 时需要获取小数 parseFloat
    return  NumberMode == 'float'? parseFloat(target) : parseInt(target);
} 

/**
 * 格式化时间
 */
export const formatDate = date => {
	var hours = padDate(date.getHours());
    var minutes = padDate(date.getMinutes());
    var seconds = padDate(date.getSeconds());
    //整理数据并返回
    return hours+':'+minutes+':'+seconds;
}
/**
 * 格式化时间
 */
export const formatDay = date => {
	var year = date.getFullYear();
    var month = padDate(date.getMonth()+1);
    var day = padDate(date.getDate());
    //整理数据并返回
    return year+'-'+month+'-'+day;
}
export const formatMonth = date => {
	var year = date.getFullYear();
    var month = padDate(date.getMonth()+1);
    //整理数据并返回
    return year+'-'+month;
}

export const padDate =  value => {    //在月份、日期、小时等小于10时在前面补0
    return value<10?'0'+value:value;
}